import React from "react";
import Metadata from "./Metadata";

function Provenance()  {
    return (
        <section id = "gnft-provenance">
            <div class = "container">
                <div class = "section-1 mb-5">
                    <h1>Gratitude NFT Provenance Record</h1>
                    <p>This page presents the provenance record of each Gratitude NFT that will ever exist. This is the final provenance record stored on the smart contract. Here is the relevant information:</p>
                    <div class = "important-info mt-3">
                        <ul>
                            <li>
                                Gratitude NFT Contract Address | <a href = "https://etherscan.io/" target = "_blank">Insert Contract Address Here</a>
                            </li>
                            <li>
                                Metadata | <a href = "https://etherscan.io/" target = "_blank">Insert Metadata Link Here</a>
                            </li>
                            <li>
                                Provenance | QmWeGPZFsKiYLNMuuwJcWCzAfHXuMN53FtwY4Wbij8ZVHG
                            </li>
                        </ul>
                    </div>
                </div>
                <div class = "section-2">
                    <h2>Provenance Record</h2>
                    <p class = "mt-2">The table below lists the original index, assigned</p>
                    <ul class = "mt-2">
                        <li>
                            Name | Edition | DNA | IPFS Hash
                        </li>
                    </ul>
                    <table class = "mt-2">
                        <tbody>

                        {
                            Metadata.map(post => {
                                return (
                                    <>
                                    <tr>
                                        <th>{post.name}</th>
                                        <th>{post.edition}</th>
                                        <th>{post.dna}</th>
                                        <th class = "ipfs-hash-provenance"><a href ={post.image} target="_blank">{post.image}</a></th>
                                    </tr>
                                    </>
                                )
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        </section>
    )
}

export default Provenance;
